import React, { useContext, useState } from 'react';
import TodoContext from './../../context/todoContext';
import { Modal, Button } from 'react-bootstrap';

const AddNewTask = () => {
    const context = useContext(TodoContext);

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);

    const handleShow = () => setShow(true);

    return (
        <div className='w-50 mx-auto fixed-bottom mb-5 p-3 wrapper'>
            <form
                className='form-inline justify-content-center'
                onSubmit={(e) => e.preventDefault()}
            >
                <div className='input-group w-100 hidden-xs hidden-sm'>
                    <input
                        type='text'
                        className='form-control rounded'
                        placeholder='اضافه کردن کار جدید'
                        value={context.todo}
                        onChange={context.todoInput}
                    />
                    <div className='input-group-prepend'>
                        <button
                            type='submit'
                            className='btn bg-transparent fa fa-plus-square text-light'
                            onClick={context.createNewTodo}
                        />
                    </div>
                </div>

                <div className='input-group-prepend hidden-md hidden-lg'>
                    <button
                        type='submit'
                        className='btn bg-transparent fa fa-plus-square text-light'
                        onClick={handleShow}
                    />
                </div>

                <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton />
                    <Modal.Body>
                        <input
                            type='text'
                            className='form-control rounded'
                            placeholder='اضافه کردن کار جدید'
                            value={context.todo}
                            onChange={context.todoInput}
                        />
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant='secondary' onClick={handleClose}>
                            بستن
                        </Button>
                        <Button
                            variant='primary'
                            onClick={context.createNewTodo}
                        >
                            ثبت کار جدید
                        </Button>
                    </Modal.Footer>
                </Modal>
            </form>
        </div>
    );
};

export default AddNewTask;
